<template>
  <div class="container">
    <el-container>
      <!-- 侧边栏 -->
      <!-- :width="isCollapse ? '64px' : '200px'" -->
      <el-aside width="isCollapse ? '80px' : '200px'">
        <div id="header_logo" @click="goHome">
          <img src="../assets/logo.png" alt="" />
        </div>
        <!-- 侧边菜单 -->

        <el-menu text-color="#fff" :collapse="isCollapse" :collapse-transition="false" router @select="handleSelect"
          :default-active="activeIndex">
          <el-menu-item index="1" route="admin">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-guanliyuan"></use>
            </svg>
            <!-- <i class="el-icon-coordinate"></i> -->
            <span slot="title">管理员</span>
          </el-menu-item>
          <el-menu-item index="2" route="user">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-yonghufill"></use>
            </svg>
            <!-- <i class="el-icon-user"></i> -->
            <span slot="title">用户</span>
          </el-menu-item>
          <el-menu-item index="3" route="order">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-jilu"></use>
            </svg>
            
            <!-- <i class="el-icon-notebook-1"></i> -->
            <span slot="title">骑行记录</span>
          </el-menu-item>
          <el-menu-item index="4" route="categories">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shangpinguanli"></use>
            </svg>
            
            <!-- <i class="el-icon-goods"></i> -->
            <span slot="title">商品管理</span>
          </el-menu-item>
          <el-menu-item index="5" route="chain">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-qukuailian"></use>
            </svg>
            <!-- <i class="el-icon-share"></i> -->
            <span slot="title">区块链溯源</span>
          </el-menu-item>
        </el-menu>
        <div class="power" @click="logout">
          <img src="@/assets/imgs/power.png" alt="">
        </div>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>

  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      // userInfoVisible: false,
      activeIndex: "/user",
      isCollapse: true,
    };
  },

  watch: {
    activeIndex(newValue) {
      // this.activeIndex = this.$route.path;

      // 如果使用 v-for 循环 可以取消上面注释
    },
  },
  methods: {
    logout() {
      window.sessionStorage.clear();
      this.$router.push("/login");
    },
    goHome() {
      this.$router.push("home");
    },
    handleSelect(keyPath) {
      sessionStorage.setItem("keyPath", keyPath);
    },
  },
  mounted() {
    this.activeIndex = sessionStorage.getItem("keyPath") || "1";
  },
};
</script>

<style lang="less" scoped>
/* .ccxkt {
  font-size: 25px;
}*/
@aside-iconwidth: 24px;
@aside-iconheight: 18px;
@aside-width: 80px;

.container {
  height: 100%;
  background-color: rgb(216, 216, 216);
  overflow: hidden;
  padding: 0 30px;
  display: flex;
  align-items: center;

  .el-container {
    height: 95%;

    border-radius: 1em;
    overflow: hidden;

    .el-aside {
      background-color: rgb(252, 252, 252);
      border-right: solid 1px;
      border-color: rgb(226, 226, 226);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 25px 0;
      #header_logo,
      .power {
        width: @aside-width;
        display: flex;
        justify-content: center;
        cursor: pointer;

        img {
          object-fit: contain;
        }
      }

      #header_logo {
        cursor: pointer;

        img {
          width: @aside-iconwidth + 20px;
          height: @aside-iconheight + 20px;
        }
      }

      .el-menu {
        position: relative;
        top: -70px;
        border-right: none;
        .el-tooltip{
          display: flex;
        }
      }

      .power {
        img {
          width: @aside-iconwidth + 8px;
          height: @aside-iconheight + 8px;
        }
      }
    }

    .el-main {
      background-color: white;
      // background-color: #dee9ec;
      // background-color: rgb(255, 249, 249);
      padding: 30px 80px 0 60px;

    }

  }

  .iconfont {
    margin-right: 10px;
  }
}
.el-menu-item{
  .icon{
    position: relative;
    color: var(--primary2);
  }
  &:first-child{
    .icon{
      left: 1px;
    }
  }
  &:nth-child(3){
    .icon{
      left:2px
    }
  }
}
.el-menu-item.is-active{
  .icon{
    color: var(--primary1)
  }
}

.icon{
  font-size: 24px;
  margin: 0 8px;
}
.el-menu--collapse{
  width: @aside-width;
}
</style>